.van-goods-action-button {
  border-radius: 0;
  height: 1.70667rem;
  height: var(--goods-action-button-height, 1.70667rem);
  line-height: 0.85333rem;
  line-height: var(--goods-action-button-line-height, 0.85333rem);
  border-width: 0;
}
.van-goods-action-button--warning {
  background: -webkit-linear-gradient(left, #ffd01e, #ff8917);
  background: linear-gradient(to right, #ffd01e, #ff8917);
  background: var(--goods-action-button-warning-color, linear-gradient(to right, #ffd01e, #ff8917));
}
.van-goods-action-button--danger {
  background: -webkit-linear-gradient(left, #ff6034, #ee0a24);
  background: linear-gradient(to right, #ff6034, #ee0a24);
  background: var(--goods-action-button-danger-color, linear-gradient(to right, #ff6034, #ee0a24));
}
.van-goods-action-button--first {
  margin-left: 0.21333rem;
  border-radius: 21.312rem 0 0 21.312rem;
  border-radius: var(--goods-action-button-border-radius, 21.312rem) 0 0 var(--goods-action-button-border-radius, 21.312rem);
}
.van-goods-action-button--last {
  margin-right: 0.21333rem;
  border-radius: 0 21.312rem 21.312rem 0;
  border-radius: 0 var(--goods-action-button-border-radius, 21.312rem) var(--goods-action-button-border-radius, 21.312rem) 0;
}
.van-goods-action-button--first.van-goods-action-button--last {
  border-radius: 21.312rem;
  border-radius: var(--goods-action-button-border-radius, 21.312rem);
}
.van-goods-action-button--plain {
  background: #fff;
  background: var(--goods-action-button-plain-color, #fff);
}
.van-goods-action-button--plain + .van-goods-action-button--plain {
  border-left-width: 0 !important;
}
.van-goods-action-button__inner {
  width: 100%;
  font-weight: 500 !important;
  font-weight: var(--font-weight-bold, 500) !important;
}
