.van-dialog {
  top: 45% !important;
  overflow: hidden;
  width: 13.65333rem;
  width: var(--dialog-width, 13.65333rem);
  font-size: 0.68267rem;
  font-size: var(--dialog-font-size, 0.68267rem);
  border-radius: 0.68267rem;
  border-radius: var(--dialog-border-radius, 0.68267rem);
  background-color: #fff;
  background-color: var(--dialog-background-color, #fff);
}
@media (max-width: 642px) {
  .van-dialog {
    width: 90%;
    width: var(--dialog-small-screen-width, 90%);
  }
}
.van-dialog taro-button-core + taro-button-core {
  margin-top: 0;
}
.van-dialog .van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX !important;
}
.van-dialog__header {
  text-align: center;
  padding-top: 1.024rem;
  padding-top: var(--dialog-header-padding-top, 1.024rem);
  font-weight: 500;
  font-weight: var(--dialog-header-font-weight, 500);
  line-height: 1.024rem;
  line-height: var(--dialog-header-line-height, 1.024rem);
}
.van-dialog__header--isolated {
  padding: 1.024rem 0;
  padding: var(--dialog-header-isolated-padding, 1.024rem 0);
}
.van-dialog__message {
  overflow-y: auto;
  text-align: center;
  -webkit-overflow-scrolling: touch;
  font-size: 0.59733rem;
  font-size: var(--dialog-message-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--dialog-message-line-height, 0.85333rem);
  max-height: 60vh;
  max-height: var(--dialog-message-max-height, 60vh);
  padding: 1.024rem;
  padding: var(--dialog-message-padding, 1.024rem);
}
.van-dialog__message-text {
  word-wrap: break-word;
}
.van-dialog__message--hasTitle {
  padding-top: 0.34133rem;
  padding-top: var(--dialog-has-title-message-padding-top, 0.34133rem);
  color: #646566;
  color: var(--dialog-has-title-message-text-color, #646566);
}
.van-dialog__message--round-button {
  padding-bottom: 0.68267rem;
  color: #323233;
}
.van-dialog__message--left {
  text-align: left;
}
.van-dialog__message--right {
  text-align: right;
}
.van-dialog__footer {
  display: -webkit-flex;
  display: flex;
}
.van-dialog__footer--round-button {
  position: relative !important;
  padding: 0.34133rem 1.024rem 0.68267rem !important;
}
.van-dialog__button {
  -webkit-flex: 1;
          flex: 1;
}
.van-dialog__confirm {
  border: 0 !important;
  color: #576b95;
}
.van-dialog__cancel {
  border: 0 !important;
  color: #646566;
}
.van-dialog-bounce-enter {
  -webkit-transform: translate3d(-50%, -50%, 0) scale(0.7);
          transform: translate3d(-50%, -50%, 0) scale(0.7);
  opacity: 0;
}
.van-dialog-bounce-leave-active {
  -webkit-transform: translate3d(-50%, -50%, 0) scale(0.9);
          transform: translate3d(-50%, -50%, 0) scale(0.9);
  opacity: 0;
}
